<template>
  <div>
    <van-nav-bar
      :title="title"
      left-text="返回"
      left-arrow
      fixed
      @click-left="onClickLeft"
    />

  </div>
</template>

<script>
  export default {
    name: "declare",
    data() {
      return {
        title: '免责声明'
      }
    },
    methods: {
      onClickLeft(){
        this.$router.back();
      }
    }
  }
</script>

<style scoped>

</style>



